@import 'src/assets/styles/variables';

.header_wrap {
  height: $headerHeight;
  width: 100%;
  display: flex;
  align-items: center;
  padding-left: 18.75%;
  padding-right: 18.75%;
  font-weight: 500;
  font-size: 16px;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.1);
  position: fixed;
  left: 0;
  top: 0;
  color: $white100;

  &.header_mobile {
    padding-left: 16px !important;
    padding-right: 16px !important;
  }

  .logo {
    width: 151px;
    height: 69px;
    margin-right: 50px;
  }

  .tab_wrap {
    display: flex;

    .tab {
      margin-right: 30px;
      cursor: pointer;
      width: max-content;
    }
  }

  .right_header {
    flex: 1;
    display: flex;
    justify-content: end;
    align-items: center;

    .find {
      margin-right: 22px;
      cursor: pointer;
    }

    div {
      cursor: pointer;
    }

    .textContainer {
      width: max-content;
    }
  }
}

@media only screen and (max-width: 480px) {
  .header_wrap {
    padding-left: 16px;
    padding-right: 16px;
    max-height: 56px;

    .tab_wrap {
      display: none;
    }

    .right_header {
      .find {
        margin-right: 12px;
      }
    }
  }
}
