.home_banner {
  .control-prev:before {
    content: url('../icons/left-arr.svg') !important;
  }

  [dir='rtl'] .control-prev:before {
    content: url('../icons/left-arr.svg') !important;
    border: none;
  }

  .control-next:before {
    content: url('../icons/right-arr.svg') !important;
    margin-left: 116px;
  }

  [dir='rtl'] .control-next:before {
    content: url('../icons/right-arr.svg') !important;
    border: none;
  }
}

.day_pass {
  .control-prev:before {
    content: url('../icons/left-arr_black.svg') !important;
  }

  [dir='rtl'] .control-prev:before {
    content: url('../icons/left-arr_black.svg') !important;
    border: none;
  }

  .control-next:before {
    content: url('../icons/right-arr_black.svg') !important;
    margin-left: 116px;
  }

  [dir='rtl'] .control-next:before {
    content: url('../icons/right-arr_black.svg') !important;
    border: none;
  }

  .carousel .control-next.control-arrow {
    opacity: 1 !important;
    right: 360px;
  }

  .carousel .control-prev.control-arrow {
    opacity: 1 !important;
    left: 360px;
  }
}

.carousel .control-prev.control-arrow:before {
  border-right: none;
}

.carousel .control-next.control-arrow:before {
  border-left: none;
}

.carousel .control-next.control-arrow {
  opacity: 1 !important;
  right: 116px;
}

.carousel .control-prev.control-arrow {
  opacity: 1 !important;
  left: 116px;
}

.carousel.carousel-slider .control-arrow:hover {
  background: none !important;
}

.control-dots {
  text-align: start !important;
  left: 18.75% !important;
  margin-bottom: 48px !important;

  .dot {
    height: 3px !important;
    width: 55px !important;
    border-radius: unset !important;
    box-shadow: none !important;
    background: white !important;
    opacity: 1 !important;
    margin-bottom: 0px !important;
    margin-left: 0 !important;
    margin-right: 10px !important;
  }
}

.carousel .control-dots .dot.selected,
.carousel .control-dots .dot:hover {
  background: #2b7fff !important;
}

.carousel .thumbs-wrapper {
  display: none;
}

@media only screen and (max-width: 480px) {
  .control-dots {
    text-align: center !important;
    left: unset !important;
    margin-bottom: 26px !important;
  }

  .carousel .control-next.control-arrow {
    right: 0 !important;
  }

  .carousel .control-prev.control-arrow {
    left: 0 !important;
  }

  .dot {
    width: 26px !important;
    height: 2px !important;
  }
}
