body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@font-face {
  font-family: 'NotoSansKR';
  src: local('NotoSansKR'), url('./assets/fonts/Noto_Sans_KR/NotoSansKR-Regular.otf') format('truetype');
  font-weight: normal;
}

@font-face {
  font-family: 'Caveat';
  src: local('Caveat'), url('./assets/fonts/caveat/Caveat-Regular.ttf') format('truetype');
  font-weight: normal;
}

@font-face {
  font-family: 'Pretendard';
  src: local('Pretendard'),
    url('./assets/fonts/pretendard/fonts/public/static/Pretendard-Regular.otf') format('truetype');
  font-weight: normal;
}
