@import 'src/assets/styles/variables';

.footer_wrap {
  margin-top: 130px;
  display: flex;
  justify-content: center;
  align-items: center;
  //background-color: $white100;
  height: 261px;
  flex-direction: column;
  .midContainer {
    /* Blue 5 */
    background: #00195e;
    display: flex;
    //flex: 1;
    align-items: center;
    justify-content: center;
    width: 100%;
    .midWrap{
      height: 143px;
      width: 1200px;
      display: flex;
      align-items: center;
    }
    .contentContainer {
      margin-left: 54px;
      .textContainer {
        gap: 13px;

        > span {
          font-family: 'NotoSansKR', sans-serif;
          font-style: normal;
          font-weight: 500;
          font-size: 12px;
          line-height: 20px;
          /* identical to box height, or 167% */

          /* Gray 3 */
          color: #c7ccda;
        }
      }
    }
  }

  .bottomContainer {
    display: flex;
    background: #1a2747;
    justify-content: center;
    align-items: center;
    width: 100%;
    .buttonWrap{
      height: 67px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 1200px;
      .imageContainer {
        .icon {
          border: #1a2747 solid 1px;
          cursor: pointer;
        }

        .icon:hover {
          border: red solid 1px;
        }
      }

      > span {
        font-family: 'NotoSansKR', sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 12px;
        line-height: 20px;
        /* identical to box height, or 167% */

        color: #ffffff;
      }
    }
  }

  .headerContainer {
    display: inline-flex;
    background-color: $white100;
    //flex: 1;
    align-items: center;
    width: 100%;
    justify-content: center;
    .headerWrap{
      width: 1200px;
      display: flex;
      height: 67px;
      flex-direction: row;
      align-items: center;
      > span {
        font-family: 'NotoSansKR', sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 20px;
        flex-shrink: 0;
        /* identical to box height, or 133% */
        /* Gray 5 */
        color: #787c88;
        width: max-content;
      }
    }
  }
}

@media only screen and (max-width: 480px) {
  .footer_wrap {
    height: unset;
    margin-top: 53px;

    .midContainer {
      height: 185px;
      justify-content: start;
      .midWrap{
        padding: 28px 16px;
        align-items: start;
        height: unset;
        width: unset;
        flex-direction: column;
      }
      .contentContainer {
        margin: 0;
        .phoneNumber {
          width: 295px;
          height: 25px;
          margin-top: 17px;
        }

        .textContainer {
          display: flex;
          flex-direction: column;
          text-align: start;
          grid-gap: unset;
          margin-top: 8px;

          > span {
            margin-left: 0 !important;
            font-size: 12px;
            line-height: 20px;
          }
        }
      }

      .icon {
        width: 127px !important;
        height: 17px !important;
      }
    }

    .headerContainer {
      padding-left: 16px;
      justify-content: start;
      .headerWrap{
        display: inline-flex;
        flex: 1;
        flex-direction: row;
        width: 100vw;
        max-width: 100%;
        height: 50px;
        align-items: center;
        overflow-y: hidden;
        > span {
          font-size: 12px;
        }
        img {
          margin-left: 15px !important;
        }
      }
    }

    .bottomContainer {
      padding: 28px 16px 23px;
      flex-direction: column;
      align-items: start;
      height: 133px;
      .buttonWrap{
        width: unset;
        flex-direction: column;
        align-items: start;
        .imageContainer{
          margin-top: 27px;
        }
      }
    }
  }
}
