@import 'src/assets/styles/variables';

.banner_wrap {
  width: 100%;
  height: 692px;
  background-color: $gray100;
  display: flex;
  justify-content: center;
  font-weight: 700;
  font-size: 20px;
  color: $black100;
  flex-direction: column;
  padding-left: 18.75%;
  padding-right: 18.75%;
  align-items: start;

  .title {
    font-weight: 500;
    font-size: 30px;
    line-height: 36px;
    color: $blue50;
    font-family: Pretendard, sans-serif;
  }

  .content {
    font-weight: 700;
    font-size: 70px;
    line-height: 84px;
    color: $white100;
    margin-top: 12px;
    font-family: Pretendard, sans-serif;
  }

  .content_sub {
    font-weight: 300;
    font-size: 25px;
    line-height: 30px;
    color: $white100;
    margin-top: 38px;
    text-align: start;

    p {
      margin: 0;
      font-family: Pretendard, sans-serif;
    }
  }
}

@media only screen and (max-width: 480px) {
  .banner_wrap {
    background-position: center center;
    background-repeat: no-repeat;
    padding-left: 16px;
    padding-right: 16px;
    align-items: center;
    justify-content: start;
    padding-top: 96px;

    .content_sub {
      font-weight: 300;
      font-size: 14px;
      line-height: 1.6;
      text-align: center;
      margin-top: 30px;
      height: 44px;
    }

    .content {
      font-weight: 700;
      font-size: 40px;
      line-height: 48px;
      white-space: break-spaces;
      margin-top: 7px;
    }

    .title {
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
    }
  }
}
