@import 'src/assets/styles/variables';

.day_pass_wrap {
  padding-top: 130px;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: $main;

  .title {
    font-weight: 700;
    font-size: 40px;
    line-height: 58px;
    color: $black100;
    display: flex;
    align-items: center;
    span {
      font-weight: 400;
      font-size: 40px;
      line-height: 50px;
      color: #0638be;
      font-family: 'Caveat', sans-serif;
      margin-right: 15px;
    }
  }

  .sub_title {
    margin-bottom: 62px;
    margin-top: 15px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #9da4b5;
  }
}

.item_pass_wrap {
  width: 100%;
  height: 480px;
  padding-left: 29.6%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: start;
  text-align: start;

  .title {
    font-weight: 400;
    font-size: 20px;
    line-height: 29px;
    color: #0638be;
  }

  .sort_content {
    margin-top: 6px;
    font-weight: 700;
    font-size: 35px;
    color: $black100;

    span {
      font-weight: 400;
      font-size: 35px;
    }
  }

  .content {
    white-space: break-spaces;
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    color: $black100;
    margin-top: 28px;
  }

  .action {
    margin-top: 38px;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    color: $white100;
    width: 201px;
    height: 58px;
    background: #0638be;
    border-radius: 100px;
    border: none;
  }
  .noteText{
    position: absolute;
    bottom: 20px;
    right: 360px;
    font-weight: 400;
    font-size: 12px;
    line-height: 22px;
    color: #787C88;
  }
}

@media only screen and (max-width: 480px) {
  .title_mobile {
    font-weight: 400;
    font-size: 16px;
    line-height: 29px;
    color: #0638be;
    text-align: start;
    margin-left: 58px;
  }
  .day_pass_wrap {
    padding-top: 78px;

    .title {
      font-size: 30px;
      line-height: 43px;

      span {
        font-size: 30px;
        line-height: 38px;
      }
    }

    .sub_title {
      margin-top: 7px;
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 33px;
    }
  }

  .item_pass_wrap {
    padding: 0 0 0 58px;
    align-items: start;
    justify-content: start;

    .sort_content {
      font-size: 28px;
      > span{
        font-size: 28px;
      }
    }

    .content {
      font-size: 14px;
      line-height: 24px;
      margin-top: 16px;
    }

    .action {
      position: absolute;
      bottom: 35px;
      margin-left: auto;
      margin-right: auto;
      left: 0;
      right: 0;
      text-align: center;
    }
  }
}
