@import 'src/assets/styles/variables';
.react-tabs {
  -webkit-tap-highlight-color: transparent;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.react-tabs__tab-list {
  padding: 0;
  margin: 0 0 25px;
}

.react-tabs__tab {
  display: inline-block;
  border-bottom: none;
  position: relative;
  list-style: none;
  padding-bottom: 8px;
  cursor: pointer;
  margin: 0 20px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #787c88;
  outline: none;
}

.react-tabs__tab--selected {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: $black100;
  border-bottom: 1px solid $black100 !important;
}

.react-tabs__tab--disabled {
  color: GrayText;
  cursor: default;
}

.react-tabs__tab-panel {
  display: none;
}

.react-tabs__tab-panel--selected {
  display: block;
  width: 100%;
}
@media only screen and (max-width: 480px) {
  .react-tabs__tab-list {
    margin: 0 0 33px;
  }
}
