@import 'src/assets/styles/variables';

.home_main_function_wrap {
  padding-top: 80px;
  width: 100%;
  display: flex;
  flex-direction: row;
  background-color: $main;
  justify-content: center;
  align-items: center;
}

.item_wrap {
  font-weight: 700;
  font-size: 20px;
  color: $black100;
  flex-direction: column;
  display: flex;
  align-items: center;

  .image_wrap {
    width: 117px;
    height: 117px;
    border-radius: 50%;
    background-color: #f5f6f8;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  > span {
    margin-top: 17px;
    font-family: 'NotoSansKR', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 23px;
    text-align: center;

    color: #000000;
  }
}

.left60 {
  margin-left: 60px;
}

.treatment_hour_wrap {
  margin-left: 47px;
  display: flex;
  flex-direction: row;
  align-items: center;

  .slice {
    width: 1px;
    height: 114px;
    background: #c7ccda;
  }

  .body {
    margin-left: 42px;
    display: flex;
    flex-direction: column;
    color: $black100;
    width: max-content;

    .title {
      font-weight: 700;
      font-size: 20px;
      line-height: 29px;
      margin-bottom: 14px;
    }

    .time {
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      margin-bottom: 12px;
    }

    .note {
      margin-top: 8px;
      font-weight: 400;
      font-size: 12px;
      line-height: 22px;
      color: #9da4b5;
    }
  }
}

@media only screen and (max-width: 480px) {
  .home_main_function_wrap {
    flex-direction: column;
    align-items: center;
    padding: 50px 16px 0;
  }

  .left60 {
    margin-left: 0;
  }

  .item_wrap {
    margin: 0 22px;

    .image_wrap {
      width: 60px;
      height: 60px;

      img {
        width: 32px;
        height: 32px;
      }
    }

    > span {
      font-size: 14px !important;
      margin-top: 10px !important;
      line-height: 20px !important;
    }
  }

  .wrap_1 {
    display: flex;
    flex-direction: row;
  }

  .wrap_2 {
    display: flex;
    flex-direction: row;
    margin-top: 10px;
  }
}
