@import 'src/assets/styles/variables';

.information_wrap {
  margin-top: 60px;
  display: flex;
  justify-content: center;
}

.information_item_wrap {
  width: 387px;
  height: 317px;
  background-color: $white100;
  margin: 0 10px;
  border: 1px solid #e9ebf1;
  box-shadow: 1px 2px 15px rgba(0, 0, 0, 0.03);
  border-radius: 10px;

  .rowWrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 1;
    padding-right: 30px;
    padding-left: 30px;

    .contentWrap {
      display: flex;

      .content {
        font-family: 'NotoSansKR', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 23px;

        color: #000000;
      }

      .img_ {
        margin-left: 6px;
      }
    }

    .time {
      //margin-left: 32px;
      font-family: 'NotoSansKR', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 23px;
      text-align: right;

      /* Gray 4 */
      color: #9da4b5;
    }
  }

  .headerWrapper {
    flex: 1;
    .headerItem {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex: 1;
      height: 84px;
      padding-right: 30px;
      padding-left: 30px;
      border-bottom: 1px solid #e9ebf1;
      > span {
        font-family: 'NotoSansKR', sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 23px;
        line-height: 33px;

        color: #000000;
      }

      .separator {
        height: 1px;
        flex: 1;
        width: 100%;
        background-color: #e9ebf1;
      }

      .end {
        display: flex;
        gap: 11.5px;

        > p {
          font-family: 'NotoSansKR', sans-serif;
          font-style: normal;
          font-weight: 500;
          font-size: 18px;
          line-height: 26px;
          /* identical to box height */

          /* Gray 4 */
          color: #9da4b5;
        }
      }
    }
  }
}

.headerContainer {
  margin-top: 130px;
  display: flex;
  justify-content: center;
  align-items: center;

  > span {
    font-family: 'NotoSansKR', 'sans-serif';
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 58px;
    /* identical to box height */
    margin-right: 15px;
    color: #000000;
  }

  > p {
    font-family: 'Caveat', 'NotoSansKR', 'sans-serif';
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 50px;
    /* Blue 3 */
    color: #0638be;
  }
}

@media only screen and (max-width: 480px) {
  .information_wrap {
    flex-direction: column;
    align-items: center;
    margin-top: 32px;
  }
  .information_item_wrap {
    width: -webkit-fill-available;
    height: unset;
    margin: 0 16px 17px;
    overflow: hidden;
    .rowWrapper {
      padding: 0 20px;
    }
    .headerWrapper {
      .headerItem {
        height: 74px;
        > span {
          font-size: 20px;
          line-height: 29px;
        }
      }
    }
    .body_wrap {
      max-height: 125px;
      overflow-y: scroll;
      margin-bottom: 25px;
    }
  }
  .headerContainer {
    margin-top: 78px;
    > span {
      font-size: 30px;
      line-height: 43px;
    }
    > p {
      font-size: 30px;
      line-height: 38px;
    }
  }
}
