@import 'src/assets/styles/variables';

.medical_course_wrap {
  padding-top: 136px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-weight: 700;
  font-size: 20px;
  line-height: 29px;
  color: $black100;
  align-items: center;
  background-color: $main;

  .list_course_wrap {
    display: grid;
    grid-template-columns: auto auto auto auto;
    margin-top: 54px;
  }

  .title {
    font-size: 40px;
    line-height: 58px;
    display: flex;
    align-items: center;
    span {
      font-family: 'Caveat', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 40px;
      line-height: 50px;
      /* Blue 3 */
      color: #0638be;
      margin-right: 15px;
    }
  }

  .sub_title {
    margin-top: 15px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #9da4b5;
  }
}

.item_wrap {
  margin: 10px 10px;
  width: 284px;
  height: 324px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  flex-direction: column;
  cursor: pointer;

  .title {
    font-weight: 700;
    font-size: 24px;
    line-height: 35px;
    color: $white100;
    white-space: pre-line;
    text-align: center;
  }

  .content {
    margin-top: 15px;
    font-weight: 400;
    font-size: 15px;
    line-height: 22px;
    color: $white100;
    white-space: pre-line;
    text-align: center;
  }
}

@media only screen and (max-width: 480px) {
  .medical_course_wrap {
    padding-top: 78px;
    font-weight: 700;

    .title {
      font-size: 30px;
      line-height: 43px;

      span {
        font-family: 'Caveat', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 30px;
        line-height: 38px;
        margin-right: 15px;
      }
    }

    .sub_title {
      font-size: 14px;
      line-height: 20px;
      margin-top: 7px;
    }

    .list_course_wrap_mobile {
      margin-top: 32px;
      width: 100%;
      max-width: 100vw;
      display: flex;
      flex-direction: row;
      overflow-x: scroll;
      justify-content: center;
      > div {
        width: 100%;
      }
    }
  }
  .item_wrap {
    text-align: center;
    margin-left: calc((100vw - 284px) / 2);
  }
}
